<template>
  <div>
    <div class="box p-a">
      <div class="b-b nav-active-blue">
        <ul class="nav nav-tabs">
          <li class="nav-item">
            <a
              class="nav-link"
              :class="{ 'active': type === 1 }"
              @click="type = 1"
            ><b>{{ $t('common.chat_robot') }}</b>
            </a>
          </li>
          <li class="nav-item">
            <a
              class="nav-link"
              :class="{ 'active': type === 2 }"
              @click="type = 2"
            ><b>{{ $t('common.plan_robot') }}</b>
            </a>
          </li>
        </ul>
      </div>
      <div class="row m-t m-b m-l-xs" v-if="type === 1">
        <button
          class="pull-left md-btn blue w-sm"
          @click="openModal('create', type)"
        >{{ $t('action.create_object', { object: $t('common.chat_robot') }) }}
        </button>
      </div>
      <table st-table="rowCollectionBasic" class="table table-striped">
        <thead>
          <tr>
            <th class="text-center" style="width: 7%;">{{ $t('common.avatar') }}</th>
            <th>{{ $t('common.nickname') }}</th>
            <th v-if="type === 1">{{ $t('common.role') }}</th>
            <th v-if="type === 2">{{ $t('common.game') }}</th>
            <th>{{ $t('common.online_time') }}</th>
            <th>{{ $t('common.speak_interval') }}</th>
            <th v-if="type === 1">{{ $t('common.chat_content') }}</th>
            <th class="text-center" style="width: 10%;">{{ $t('common.status') }}</th>
            <th class="text-center" style="width: 10%;">{{ $t('common.operate') }}</th>
          </tr>
        </thead>
        <tbody v-if="!pageLoading">
          <tr
            v-for="element in filerRobots"
            :key="element.id"
          >
            <td class="p-r-xs">
              <img
                :src="element.user_robot.avatar || defaultAvatar"
                id="circle"
                style="height:48px; width:48px;"
              />
            </td>
            <td>{{ element.user_robot.nickname }}</td>
            <td v-if="type === 1">{{ `${element.user_robot.roles[0].display_name}` }}</td>
            <td v-if="type === 2">{{ $t('game.' + element.game_code) }}</td>
            <td>
              <span>{{ element.day_of_week && `${$t('week.week')}: ${tranformWeek(element.day_of_week)}` }}</span>
              <br/>
              <span>{{ `${$t('common.time')}: ${element.on_time} ~ ${element.off_time}` }}</span>
            </td>
            <td>{{ (element.speak_interval && element.speak_interval + $t('common.minute')) || '-' }}</td>
            <td v-if="type === 1" v-html="textBreakAll(element.chat_content)"></td>
            <td class="text-center p-r-0">
              <label
                class="label m-r-xs "
                :class="element.enabled ? 'success' : 'danger'"
              >{{ element.enabled ? $t('status.active') : $t('status.inactive') }}
              </label>
              <label class="md-switch">
                <input
                  type="checkbox"
                  v-model="element.enabled"
                  @change="updateRobot({ mode: 'update', field: 'status' }, type, Object.assign({}, element, {
                    nickname: element.user_robot.nickname,
                    role: element.user_robot.roles[0].display_name === $t('role.manager') ? 1 : 2,
                    day_of_week: `${element.day_of_week}`
                  }))"
                />
                <i class="success"></i>
              </label>
            </td>
            <td class="text-center">
              <button
                class="btn btn-xs btn-icon blue"
                @click="openModal('update', type, Object.assign({}, element, {
                  avatar: element.user_robot.avatar,
                  hasAvatar: !!element.user_robot.avatar,
                  hasAvatarFile: false,
                  avatarFile: '',
                  nickname: element.user_robot.nickname,
                  role: element.user_robot.roles[0].display_name === '管理员' ? 1 : 2,
                  on_time: {
                    HH: element.on_time.split(':')[0],
                    mm: element.on_time.split(':')[1]
                  },
                  off_time: {
                    HH: element.off_time.split(':')[0],
                    mm: element.off_time.split(':')[1]
                  }
                }))"
              ><i class="fa fa-edit"></i>
              </button>
              <button
                class="btn btn-xs btn-icon danger"
                @click="openModal('delete', type, Object.assign({}, element, {
                  nickname: element.user_robot.nickname
                }))"
                v-if="type !== 2"
              ><i class="fa fa-trash-o"></i>
              </button>
            </td>
          </tr>
        </tbody>
      </table>

      <div class="row" v-if="pageLoading">
        <div class="text-center">
          <i class="fa fa-spin fa-spinner"></i> <b>{{ $t('common.loading') }}</b>
        </div>
      </div>

      <div class="row" v-if="!pageLoading && !filerRobots.length">
        <div class="text-center m-t">
          <span>{{ $t('common.no_data') }}</span>
        </div>
      </div>
    </div>

    <div class="modal" v-if="modal.showModal">
      <div class="modal-backdrop fade in" @click="modal.showModal = false">
      </div>
      <form class="form">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">
                {{ modal.mode && $t(`action.${modal.mode}_object`, {
                  object: modal.type === 1 ? $t('common.chat_robot') : $t('common.plan_robot')
                }) }}
              </h5>
            </div>
            <div class="modal-body p-lg" v-if="modal.mode !== 'delete'">
              <div class="row">
                <div class="col-sm-4 col-sm-offset-4 text-center">
                  <div class="avatarNav">
                    <button
                      type="button"
                      class="btn btn-xs btn-icon navDelete"
                      @click="clearImg"
                      v-if="modal.robot.hasAvatar"
                    >
                      <i class="fa fa-times"></i>
                    </button>
                  </div>
                  <label class="avatar m-b-0">
                    <img
                      :src="modal.robot.avatar"
                      id="circle"
                      v-if="modal.robot.hasAvatar"
                    />
                    <img
                      :src="defaultAvatar"
                      id="circle"
                      v-else
                    />
                    <div class="uploadOverlay">
                      <i class="fa fa-cloud-upload"></i>
                    </div>
                    <h6 class="m-t-sm">{{ $t('action.update_object', {
                      object: $t('common.avatar')}) }}
                    </h6>
                    <input
                      type="file"
                      accept="image/*"
                      style="display: none"
                      @change="syncImg($event)"
                    />
                  </label>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label">{{ $t('common.nickname') }}</label>
                </div>
                <div class="col-sm-4">
                  <input
                    class="form-control"
                    v-model.trim="modal.robot.nickname"
                  />
                </div>
              </div>
              <div class="row m-t-xs b-b" v-if="modal.type === 1">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label">{{ $t('common.role') }}</label>
                </div>
                <div class="col-sm-6 p-t-sm">
                  <label class="sm-check m-r">
                    <input class="c-radio" type="radio" value="2" v-model="modal.robot.role">
                    <i class="blue m-r-xs"></i>
                    {{ $t('role.member') }}
                  </label>
                  <label class="sm-check m-r">
                    <input class="c-radio" type="radio" value="1" v-model="modal.robot.role">
                    <i class="blue m-r-xs"></i>
                    {{ $t('role.manager') }}
                  </label>
                </div>
              </div>
              <div class="row m-t-xs b-b" v-else-if="modal.type === 2">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label">{{ $t('common.game') }}</label>
                </div>
                <div class="col-sm-6">
                  <label class="form-control-label p-l-0">{{ $t('game.' + modal.robot.game_code) }}</label>
                </div>
              </div>
              <div class="row m-t-xs">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label">{{ $t('common.online_time') }}</label>
                </div>
                <div class="col-sm-6 p-t-sm">
                  {{ $t('week.week') }}
                  <label class="m-l">
                    <input
                      type="checkbox"
                      :checked="isSelectAll"
                      @change="toggleSelectAllDays($event)"
                    />
                    <i class="dark-white"></i>
                    {{ $t('common.select_all') }}
                  </label>
                  <div class="checkbox">
                    <label class="m-r" v-for="i in 7" :key="i">
                      <input
                        type="checkbox"
                        name="week"
                        v-model="modal.checkboxDay[i%7]"
                      />
                      <i class="dark-white"></i>
                      {{ $t('week.' + (i % 7)) }}
                    </label>
                  </div>
                </div>
              </div>
              <div class="row m-t-xs">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label">{{ $t('common.on_time') }}</label>
                </div>
                <div class="col-sm-6">
                  <time-picker v-model="modal.robot.on_time"/>
                </div>
              </div>
              <div class="row m-t-xs">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label">{{ $t('common.off_time') }}</label>
                </div>
                <div class="col-sm-6">
                  <time-picker v-model="modal.robot.off_time"/>
                </div>
              </div>
              <div class="row m-t-xs m-b-sm">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label">{{ $t('common.speak_interval') }}</label>
                </div>
                <div class="col-sm-3 p-r-0">
                  <input
                    type="number"
                    style="width:140px;"
                    class="form-control"
                    v-model="modal.robot.speak_interval"
                  />
                </div>
                <div class="col-sm-3 text-left p-l-0">
                  <label class="form-control-label">{{ $t('common.minute')}}</label>
                </div>
              </div>
              <div class="row p-t-sm b-t" v-if="modal.type === 1">
                <div class="col-sm-12 m-t-sm text-center">
                  <h6>{{ $t('common.chat_content') }}</h6>
                </div>
                <div class="col-sm-offset-2 col-sm-8">
                  <textarea
                    class="form-control"
                    rows="5"
                    v-model="modal.robot.chat_content"
                  />
                </div>
              </div>
            </div>
            <div class="modal-body p-lg" v-else>
              <span>{{ $t('message.sure_to_do', {
                object: `${modal.type === 1 ? $t('common.chat_robot') : $t('common.plan_robot')}(${modal.robot.nickname || $t('common.no_setting')})`,
                operate: $t('action.delete')
              }) }}
              </span>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn dark-white p-x-md w-xs" @click="modal.showModal = false">
                {{ $t('action.cancel') }}
              </button>
              <button
                type="button"
                class="btn blue p-x-md w-xs"
                @click="updateRobot({ mode: modal.mode }, type, Object.assign({}, modal.robot, {
                  day_of_week: `${modal.robot.day_of_week}`,
                  on_time: (modal.robot.on_time.HH && modal.robot.on_time.mm) ? `${modal.robot.on_time.HH}:${modal.robot.on_time.mm}` : '',
                  off_time: (modal.robot.off_time.HH && modal.robot.off_time.mm) ? `${modal.robot.off_time.HH}:${modal.robot.off_time.mm}` : ''
                }))"
              >
                <span v-if="modal.loading"><i class="fa fa-spin fa-spinner"></i></span>
                <span v-else>{{ $t('action.confirm') }}</span>
              </button>
            </div>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import api from '../api'
import TimePicker from 'vue2-timepicker'
import $ from '../utils/util'
const defaultAvatar = require('../assets/avatar.png')
export default {
  data () {
    return {
      pageLoading: true,
      type: 1,
      robots: [],
      modal: {
        showModal: false,
        mode: '',
        checkboxDay: {},
        robot: {}
      },
      updateFields: [
        'nickname',
        'role',
        'day_of_week',
        'on_time',
        'off_time',
        'speak_interval',
        'enabled'
      ],
      defaultAvatar
    }
  },
  created () {
    this.getRobots()
  },
  computed: {
    filerRobots () {
      return this.robots.filter(element => element.type === this.type)
    },
    isSelectAll () {
      return this.modal.robot.day_of_week.length === 7
    }
  },
  watch: {
    'modal.checkboxDay': {
      handler (newObj) {
        this.modal.robot.day_of_week = Object.keys(newObj).filter(e => newObj[e])
      },
      deep: true
    }
  },
  methods: {
    getRobots () {
      this.$http.get(`${api.robot}?type=1,2`).then(data => {
        data.forEach(element => {
          element.day_of_week = this.splitDayOfWeek(element)
        })
        this.robots = Object.assign([], data.sort((a, b) => b.id - a.id))
        this.pageLoading = false
      })
    },
    splitDayOfWeek (element) {
      return element.day_of_week && element.day_of_week.split(',')
    },
    textBreakAll (str) {
      return str && str.replace(/(\r\n|\r|\n)/g, '<br/>')
    },
    updateRobot ({ mode = '', field = '' }, type = 1, resultRobot) {
      let result = new window.FormData()
      result.append('type', type)
      let fields = [...this.updateFields, type === 1 ? 'chat_content' : 'game_code']
      let unfinished = fields.filter(key => resultRobot[key] === '' || resultRobot[key] === undefined)
      if (unfinished.length > 0 && mode !== 'delete') {
        $.notify({
          message: this.$t('action.please_fill_object', {
            object: unfinished.map(key => this.$t('common.' + key)).join(',')
          }),
          type: 'danger'
        })
        return
      } else {
        fields.forEach(key => {
          result.append(key, resultRobot[key])
        })
      }
      if (resultRobot.hasAvatarFile) {
        result.append('avatar', resultRobot.avatarFile)
      } else if (!resultRobot.hasAvatar) {
        result.append('avatar', '')
      }

      this.modal.loading = true
      if (mode === 'create') {
        this.$http.post(api.robot, result).then(data => {
          data.day_of_week = this.splitDayOfWeek(data)
          this.robots = [data, ...this.robots]
          this.notify(mode, 'success', 'success', true)
        }, error => {
          this.notify(mode, 'danger', 'failed', false, false, error)
        })
      } else if (mode === 'update') {
        this.$http.put(`${api.robot}${resultRobot.id}/`, result).then(data => {
          this.notify(mode, 'success', 'success', true, field !== 'status')
        }, error => {
          this.notify(mode, 'danger', 'failed', false, field === 'status', error)
        })
      } else if (mode === 'delete') {
        this.$http.delete(`${api.robot}${resultRobot.id}/`).then(data => {
          this.notify(mode, 'success', 'success', true, true)
        }, error => {
          this.notify(mode, 'danger', 'failed', false, false, error)
        })
      }
    },
    openModal (mode = '', type = 1, robot = {
      avatar: null,
      hasAvatar: false,
      avatarFile: '',
      nickname: '',
      role: 2,
      day_of_week: [],
      speak_interval: '',
      on_time: {
        HH: '0',
        mm: '0'
      },
      off_time: {
        HH: '0',
        mm: '0'
      },
      chat_content: '',
      game_code: '',
      enabled: true
    }) {
      this.modal = Object.assign({
        mode,
        type,
        robot,
        checkboxDay: {},
        showModal: true,
        loading: false
      })
      this.modal.robot.day_of_week.forEach(element => {
        this.$set(this.modal.checkboxDay, element, true)
      })
    },
    syncImg (e) {
      let file = e.target.files[0]

      if (file) {
        var reader = new FileReader()
        reader.onload = (e) => {
          this.modal.robot.avatar = e.target.result
        }
        reader.readAsDataURL(file)
        this.modal.robot.avatarFile = file
        this.modal.robot.hasAvatar = true
        this.modal.robot.hasAvatarFile = true
      }
    },
    clearImg () {
      this.modal.robot.hasAvatar = false
      this.modal.robot.hasAvatarFile = false
    },
    tranformWeek (days = []) {
      return days.map(element => this.$t('week.' + element)).join(',')
    },
    toggleSelectAllDays (e) {
      for (let i = 0; i < 7; ++i) {
        this.$set(this.modal.checkboxDay, i, e.target.checked)
      }
    },
    closeModal () {
      this.modal.showModal = false
    },
    notify (operate = 'update', type = 'success', status = 'success', closeModal = false, getRobots = false, addMsg = '') {
      this.modal.loading = false
      $.notify({
        message: this.$t('action.' + operate) + this.$t('status.' + status) + (addMsg && ` (${addMsg})`),
        type
      })
      closeModal && this.closeModal()
      getRobots && this.getRobots()
    }
  },
  components: {
    TimePicker
  }
}
</script>

<style lang="scss" scoped>
#circle {
  width: 108px;
  height: 108px;
  line-height: 108px;
  background: lightgray;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}
.avatar {
  width: 108px;
  cursor: pointer;
  .uploadOverlay {
    background-color: rgba(33, 152, 243, 0.87);
    transition: all .5s ease;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 100%;
    position: absolute;
    height: 108px;
    width: 108px;
    left: 0;
    top: 0;
    opacity: 0;

    img {
      border-radius: 100%;
      width: 108px;
      margin-left: 1px;
    }

    i {
      vertical-align: bottom;
      display:inline-block;
      line-height: 108px;
      text-align: center;
      font-size: 40px;
      height: 110px;
      width: 110px;
      left: -2px;
      top:-2px;
      color:white;
    }
  }

  &:hover .uploadOverlay {
    transition: all .5s ease;
    opacity: 1;
    top: 0;
  }
}
.avatarNav {
  position: absolute;
  top: 0;
  right: 0;
  width: 42px;
  height: 100%;
  font-size: 30px;
  color: #fff;

  .navDelete {
    position: absolute;
    color: black;
    top: 0;
    left: 0;
  }
}
</style>



